<template>
	<div class="header">
		<div class="content-wrapper">
			<div class="avatar">
				<img width="64" height="64" :src="seller.avatar">
			</div>
			<div class="content">
				<div class="title">
					<span class="brand"><img></span>
					<span class="name">{{seller.name}}</span>
				</div>
				<div class="miaoshu">{{seller.description}}/{{seller.deliveryTime}}分钟送达
				</div>
				<div v-if="seller.supports" class="support">
					<supporticon :size="1" :type="seller.supports[2].type"></supporticon>
					<span class="text">{{seller.supports[2].description}}</span>
				</div>
			</div>
			<div class="support-count" v-if="seller.supports" @click="showDetail">
				<span class="count">{{seller.supports.length}}个</span>
				<i class="icon-keyboard_arrow_right"></i>
			</div>
		</div>
		<div class="bulletin-wrapper" @click="showDetail">
			<span class="bulletin-title"></span>
			<span class="bulletin-text">{{seller.bulletin}}</span>
			<i class="icon-keyboard_arrow_right"></i>
		</div>
		<div class="background">
			<img :src="seller.avatar" width="100%" height="100%">
		</div>
		<div class="detail fade-transition" v-show="detailshow">
			<div class="detail-wrapper clearfix">
				<div class="detail-main">
					<h1 class="detail-name">{{seller.name}}</h1>
					<div class="star-wrapper">
						<star :size="48" :score="seller.score"></star>
					</div>
					<div class="title">
						<div class="line"></div>
						<div class="text">优惠信息</div>
						<div class="line"></div>
					</div>
					<ul class="supports" v-if="seller.supports">
						<li class="support-item"v-for="(list,index) in seller.supports">
							<supporticon :size="2" :type="seller.supports[index].type"></supporticon>
							<span class="text">{{list.description}}</span>             
						</li>
					</ul>
					<div class="title">
						<div class="line"></div>
						<div class="text">商家公告</div>
						<div class="line"></div>
					</div>
					<div class="bulletin">
						<p class="content">{{seller.bulletin}}</p>
					</div>          
				</div>
			</div>
			<div class="detail-close">
				<i class="icon-close" @click="closeDetail"></i>
			</div>
		</div>
	</div>
</template>
<script>
	import star from "../star/star";
	import supporticon from "../supporticon/supporticon";
	export default {
		name: 'header',
		props: {
			seller:{
				type:Object
			}
		},
		data() {
			return {
				detailshow: false,
			}
		},
		methods: {
			showDetail() {
				this.detailshow = true;
			},
			closeDetail() {
				this.detailshow = false;
			}
		},
		created() {
			this.classMap=['decrease','discount','special','invoice','guarantee']
		},
		components:{
			star,supporticon
		}
	}
</script>
<style>
	.header{
		position: relative;
		overflow: hidden;
		color: #fff;
		background: rgba(7,17,27,0.5);
	}
	.header .content-wrapper{
		position: relative;
		padding: 24px 12px 18px 24px;
		font-size: 0;
	}
	.header .content-wrapper .avatar {
		display: inline-block;
		vertical-align: top;
	}
	.header .content-wrapper .content {
		display: inline-block;
		margin-left: 16px;
	}
	.header .content-wrapper .content .title {
		margin: 2px 0 8px 0;
	}
	.header .content-wrapper .content .title .brand {
		display: inline-block;
		vertical-align: top;
		width: 30px;
		height: 18px;
		background-image: url('brand@2x.png');
		background-size: 30px 18px;
		background-repeat: no-repeat;
	}
	.name{
		font-size: 16px;
		font-weight: bold;
		line-height: 18px;
		margin-left: 6px;
	}
	.miaoshu{
		font-size: 12px;
		line-height: 12px;
		margin-bottom:10px;
	}
	.icon{
		width: 12px;
		height: 12px;
		display:inline-block;
		margin-right: 4px;
		vertical-align: top;
		background-repeat:no-repeat;
		background-size: 12px 12px;
		background-repeat: no-repeat;
	}
	.decrease{
		background-image:url("./decrease_1@2x.png");
	}
	.discount{
		background-image:url("./discount_1@2x.png");
	}
	.guarantee{
		background-image:url("./guarantee_1@2x.png");
	}
	.invoice{
		background-image:url("./invoice_1@2x.png");
	}
	.special{
		background-image:url("./special_1@2x.png");
	}
	.text{
		font-size: 10px;
		line-height: 12px;
	}
	.header .content-wrapper .support-count {
		position: absolute;
		right: 12px;
		bottom: 14px;
		padding: 0 8px;
		height: 24px;
		line-height: 24px;
		border-radius: 14px;
		background: rgba(0,0,0,0.2);
		text-align: center;
	}
	.header .content-wrapper .support-count .count {
		vertical-align: top;
		font-size: 10px;
	}
	.header .content-wrapper .support-count .icon-keyboard_arrow_right {
		margin-left: 2px;
		line-height: 24px;
		font-size: 10px;
	}
	.header .bulletin-wrapper {
		position: relative;
		height: 28px;
		line-height: 28px;
		padding: 0 22px 0 12px;
		white-space: nowrap;
		overflow: hidden;
		text-overflow: ellipsis;
		background: rgba(7,17,27,0.2);
	}
	.header .bulletin-wrapper .bulletin-title {
		display: inline-block;
		vertical-align: top;
		margin-top: 8px;
		width: 22px;
		height: 12px;
		background-image: url(bulletin@2x.png);
		background-size: 22px 12px;
		background-repeat: no-repeat;
	}
	.header .bulletin-wrapper .bulletin-text {
		vertical-align: top;
		margin: 0 4px;
		font-size: 10px;
	}
	.header .bulletin-wrapper .icon-keyboard_arrow_right {
		position: absolute;
		font-size: 10px;
		right: 12px;
		top: 8px;
	}
	.header .background {
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		z-index: -1;
		-webkit-filter: blur(10px);
		filter: blur(10px);
	}
	.header .detail{
		top:0;
		left: 0;
		position: fixed;
		z-index: 100;
		width: 100%;
		height: 100%;
		overflow:auto;
		background: rgba(7,17,27,0.8);
	}
	.header .detail.fade-transition {
    opacity: 1;
    background: rgba(7,17,27,0.8);
}
	.header .detail .detail-wrapper {
		width: 100%;
		min-height: 100%;
	}
	.header .detail .detail-wrapper .detail-main {
		margin-top: 64px;
		padding-bottom: 64px;
	}
	.header .detail .detail-close {
		position: relative;
		width: 32px;
		height: 32px;
		margin: -64px auto 0 auto;
		clear: both;
		font-size: 32px;
	}
	.header .detail .detail-name{
		line-height: 16px;
		text-align: center;
		font-size: 16px;
		font-weight: 700;
	}
	.header .detail .detail-wrapper .detail-main .star-wrapper {
		margin-top: 18px;
		padding: 2px 0;
		text-align: center;
	}
	.header .detail .detail-wrapper .detail-main .title {
		display: -webkit-box;
		display: -ms-flexbox;
		display: flex;
		width: 80%;
		margin: 28px auto 24px auto;
	}
	.header .detail .detail-wrapper .detail-main .title .line {
		-webkit-box-flex: 1;
		-ms-flex: 1;
		flex: 1;
		position: relative;
		top: -6px;
		border-bottom: 1px solid rgba(255,255,255,0.2);
	}
	.header .detail .detail-wrapper .detail-main .title .text {
		padding: 0 12px;
		font-weight: 700;
		font-size: 14px;
	}
	.header .detail .detail-wrapper .detail-main .bulletin {
		width: 80%;
		margin: 0 auto;
	}
	.header .detail .detail-wrapper .detail-main .bulletin .content {
		padding: 0 12px;
		line-height: 24px;
		font-size: 12px;
	}
	.header .detail .detail-wrapper .detail-main .supports {
		width: 80%;
		margin: 0 auto;
	}
	.header .detail .detail-wrapper .detail-main .supports .support-item {
		padding: 0 12px;
		margin-bottom: 12px;
		font-size: 0;
	}
	.header .detail .detail-wrapper .detail-main .supports .support-item .icon {
		display: inline-block;
		width: 16px;
		height: 16px;
		vertical-align: top;
		margin-right: 6px;
		background-size: 16px 16px;
		background-repeat: no-repeat;
	}
	.header .detail .detail-wrapper .detail-main .supports .support-item .text {
		line-height: 16px;
		font-size: 12px;
	}
	.header .detail .detail-wrapper .detail-main .bulletin .content {
		padding: 0 12px;
		line-height: 24px;
		font-size: 12px;
	}

</style>



