import Vue from 'vue'
import Router from 'vue-router'
import v_goods from "../components/goods/goods"
import v_ratings from "../components/ratings/ratings"
import v_seller from "../components/seller/seller"

Vue.use(Router)

export default new Router({
  routes: [
  { path: '/ratings', component: v_ratings },
  { path: '/goods', component: v_goods },
  { path: '/seller', component: v_seller },
  ]
})


