<template>
	<div class="goods">
		<div class="menu-wrapper">
			<ul>
				<li v-for="item in goods" class="menu-item">
					<span class="text border-1px">
						<supporticon :size="3" :type="item.type" v-show="item.type>0"></supporticon>{{item.name}}
					</span>
				</li>
			</ul>
		</div>
		<div class="foods-wrapper">
			<ul>
				<li v-for="item in goods" class="food-list">
					<h1 class="title">{{item.name}}</h1>
					<ul>
						<li v-for="food in item.foods" class="food-item border-1px">
							<div class="icon">
								<img :src="food.icon" width="57" height="57">
							</div>
							<div class="content">
								<h2 class="name">{{food.name}}</h2>
								<p class="desc">{{food.description}}</p>
								<div class="extra">
									<span class="count">月售{{food.sellCount}}份</span>
									<span>好评率{{food.rating}}%</span>
								</div>	  
								<div class="price">
									<span class="now">￥{{food.price}}</span>
									<span v-show="food.oldPrice" class="old">￥{{food.oldPrice}}</span>
								</div>
								<div class="cartcontrol-wrapper">
									<cartControl></cartControl>
								</div>
							</div>
						</li>
					</ul>
				</li>
			</ul>
		</div>
		<shopcart></shopcart>
	</div>
</template >
<script>
	import supporticon from "../supporticon/supporticon";
	import shopcart from '../shopcart/shopcart.vue';
	import cartControl from '../cartControl/cartControl.vue';
	var data = require("@/common/json/data");
	const ERR_OK = 0;
	export default {		
		name: 'goods',
		props: {
			seller: {
				type: Object
			}
		},		
		data(){
			return{
				goods:[],
			}
		},
		created(){
				 this.goods = data.goods;		
		},
		components:{
			supporticon,
			shopcart,
			cartControl,
		}
	}
</script>
<style>
	.goods{
		display: flex;
		position: absolute;
		top:174px;
		width: 100%;
		bottom: 46px;
		overflow: hidden;

	}
	.goods .menu-wrapper{
		flex: 0 0 80px;
		width: 80px;
		background: #f3f5f7;
	}
	.goods .foods-wrapper{
		flex: 1
	}
	.goods .menu-wrapper .menu-item {
		display: table;
		height: 54px;
		width: 56px;
		padding: 0 12px;
		line-height: 14px;
	}
	.goods .menu-wrapper .menu-item .text {
		display: table-cell;
		width: 56px;
		vertical-align: middle;
		position: relative;
		font-size: 12px;
	}
	.goods .foods-wrapper .title {
		padding-left: 14px;
		height: 26px;
		line-height: 26px;
		border-left: 2px solid rgb(217, 221, 225);
		font-size: 12px;
		color: rgb(147, 153, 159);
		background: rgb(243, 245, 247);
	}
	.goods .foods-wrapper .food-item {
		display: -webkit-box;
		display: -ms-flexbox;
		display: flex;
		margin: 18px;
		padding-bottom: 18px;
		position: relative;
	}
	.goods .foods-wrapper .food-item .icon {
		-webkit-box-flex: 0;
		-ms-flex: 0 0 57px;
		flex: 0 0 57px;
		margin-right: 10px;
	}
	.goods .foods-wrapper .food-item .content {
		-webkit-box-flex: 1;
		-ms-flex: 1;
		flex: 1;
	}
	.goods .foods-wrapper .food-item .content .name {
		margin: 2px 0 8px 0;
		height: 14px;
		line-height: 14px;
		font-size: 14px;
		color: rgb(7, 17, 27);
	}
	.goods .foods-wrapper .food-item .content .desc {
		line-height: 12px;
		margin-bottom: 8px;
	}
	.goods .foods-wrapper .food-item .content .desc, .goods .foods-wrapper .food-item .content .extra {
		line-height: 10px;
		font-size: 10px;
		color: rgb(147, 153, 159);
	}
	.goods .foods-wrapper .food-item .content .extra .count {
		margin-right: 12px;
	}
	.goods .foods-wrapper .food-item .content .price {
		font-weight: 700;
		line-height: 24px;
	}
	.goods .foods-wrapper .food-item .content .price .now {
		margin-right: 8px;
		font-size: 14px;
		color: rgb(240, 20, 20);
	}
	.goods .foods-wrapper .food-item .content .price .old {
		text-decoration: line-through;
		font-size: 10px;
		color: rgb(147, 153, 159);
	}
	.goods .foods-wrapper .food-item .content .price .old {
		text-decoration: line-through;
		font-size: 10px;
		color: rgb(147, 153, 159);
	}
	.goods .foods-wrapper .food-item .content .cartcontrol-wrapper {
		position: absolute;
		right: 0;
		bottom: 12px;
	}
</style>


