<template>
  <div class="cartcontrol">
   <div class="cart-decrease">
     <span class="inner icon-remove_circle_outline"></span>
   </div>
   <div class="cart-count">12</div>
   <div class="cart-add icon-add_circle"></div>
 </div>
</template>
<script>
  import Vue from 'vue';
  export default {
    props: {
      food: {
        type: Object
      }
    },
    methods: {
      addCart(event) {
        if (!event._constructed) {
          // 去掉自带click事件的点击
          return;
        }
        if (!this.food.count) {
          Vue.set(this.food, 'count', 1);
        } else {
          this.food.count++;
        }
//        event.srcElement.outerHTML
        this.$emit('increment', event.target); // 子组件通过 $emit触发父组件的方法 increment   还
      },
      decreaseCart(event) {
        if (!event._constructed) {
          // 去掉自带click事件的点击
          return;
        }
        this.food.count--;
      }
    }
  };
</script>
<style>
  .cartcontrol {
    font-size: 0;
  }
  .cartcontrol .cart-decrease {
    display: inline-block;
    padding: 6px;
    -webkit-transition: all 0.4s linear;
    transition: all 0.4s linear;
  }
  .cartcontrol .cart-decrease .inner {
    display: inline-block;
    line-height: 24px;
    font-size: 24px;
    color: rgb(0, 160, 220);
    -webkit-transition: all 0.4s linear;
    transition: all 0.4s linear;
    -webkit-transform: rotate(0);
    transform: rotate(0);
  }
  .cartcontrol .cart-count {
    display: inline-block;
    vertical-align: top;
    width: 12px;
    padding-top: 6px;
    line-height: 24px;
    text-align: center;
    font-size: 10px;
    color: rgb(147, 153, 159);
  }
  .cartcontrol .cart-add {
    display: inline-block;
    padding: 6px;
    line-height: 24px;
    font-size: 24px;
    color: rgb(0, 160, 220);
  }
</style>




