        <div class="rank">高于周边商家20%</div>
      </div>
      <div class="overview-right">
       <div class="score-wrapper">
         <span class="title">服务态度</span>
         <star :size="36" :score="2"></star>
         <span class="score">2.1</span>
       </div>
       <div class="score-wrapper">
         <span class="title">商品评分</span>
         <star :size="36" :score="4.2"></star>
         <span class="score">4.3</span>
       </div>
       <div class="delivery-wrapper">
         <span class="title">送达时间</span>
         <span class="delivery">32分钟</span>
       </div>
     </div>
   </div>
   <div class="split"></div>
   <div class="ratingselect">
     <div class="ratingstype"></div>
     <div class="switch">
       <span class="icon-check_circle"></span>
       <span class="text"></span>
     </div>
   </div>
   <div class="rating-wrapper"></div>
 </div>
</div>
</template >
<script>
  import star from "../star/star";
  var data = require("@/common/json/data");
  const ERR_OK = 0;
  const  result =0 ;
  export default {
    name: 'ratings',
    data(){
     return{
      ratings:[],
    }
  },
  methods:{

         // // for(i=0,i<ratings.length,i++){
         //  result += ratings[i].score;
         // }
       },
       created(){
							  	this.ratings = data.ratings;
			
       },
       components:{
        star
      }
    }
  </script>
  <style>
   .ratings .overview {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    padding: 18px 0;
  }
  .ratings .overview .overview-left {
    -webkit-box-flex: 0;
    -ms-flex: 0 0 137px;
    flex: 0 0 137px;
    padding: 6px 0;
    width: 137px;
    border-right: 1px solid rgba(7,17,27,0.1);
    text-align: center;
  }
  .ratings .overview .overview-left .score {
    margin-bottom: 6px;
    line-height: 28px;
    font-size: 24px;
    color: rgb(255, 153, 0);
  }
  .ratings .overview .overview-left .title {
    margin-bottom: 8px;
    line-height: 12px;
    font-size: 12px;
    color: rgb(7, 17, 27);
  }
  .ratings .overview .overview-left .rank {
    line-height: 10px;
    font-size: 10px;
    color: rgb(147, 153, 159);
  }
  .ratings .overview .overview-right {
    -webkit-box-flex: 1;
    -ms-flex: 1;
    flex: 1;
    padding: 6px 0 6px 24px;
  }
  .ratings .overview .overview-right .score-wrapper {
