<template>
  <div id="app">
    <v_header :seller="seller"></v_header>
    <div class="tab">
     <div class="tab-item">
       <router-link to="/goods">商品</router-link></div>
       <div class="tab-item">
         <router-link to="/ratings">评论</router-link>
       </div>
       <div class="tab-item">
         <router-link to="/seller">商家</router-link></div>
       </div>
       <router-view></router-view>
     </div>
   </template>
   <script>
    import v_header from "./components/header/header";
    var data = require("./common/json/data");
    export default {
      name: 'app',
      data: function () {
        return {
          seller:{}
        }
      },
          methods: {
            getCustomers: function() {
              this.$http.jsonp('data.json')
                .then(function(response) { //response传参，可以是任何值
                  this.$set('tasks', response.data)
                })
                .catch(function(response) {
                  console.log(response)
                })
            }
          },



      created(){
  //       this.$http.get('static/data.json').then(response => {

  //   // get body data
  //        response = response.body;
  //        this.seller = response.seller;
  //        console.log(this.seller);
  // }, response => {
  //   // error callback
  // });
     this.seller = data.seller;
      },
      components:{
       v_header
     }
   }
 </script>
 <style>
   .tab{
    display: flex;
    width: 100%;
    height: 40px;
    line-height: 40px;
    border: 1px solid rgba(7,17,27,0.1);
  }
  .tab .tab-item{
    flex: 1;
    text-align: center;
  }
  .tab .tab-item a{
    display: block;
    font-size: 14px;
    color: rgb(77,85,93);
  }
  .tab-item a.router-link-active{
    color: rgb(240,20,20)
  }
</style>



