<template>
  <span class="support" :class="supporticonsize">
    <img :class="classMap[type]+size" class="supporticon">
  </span>
	<!-- 传入size（1：无背景12x12 ，2：无背景18x18，3:带背景12px 4：带背景 18x18）和 种类type（0减价 1打折 2特价 3带发票 4外卖保计划） -->	  
</template>
<script>
	export default{
		props: {
			size: {
				type: Number
			},
			type: {
				type: Number
			}
		},
		created() {
			this.classMap=['decrease','discount','special','invoice','guarantee']
		},
		computed:{
			supporticonsize(){
				return 'support'+ this.size;
			},
		}
	}
</script>
<style>
	.support1 .supporticon,.support3 .supporticon{
    display: inline-block;
    width: 12px;
    height: 12px;
    margin-right: 4px;
    background-size: 12px 12px;
    background-repeat: no-repeat;
	}
	.support2 .supporticon,.support4 .supporticon{
    display: inline-block;
    width: 16px;
    height: 16px;
    vertical-align: top;
    margin-right: 6px;
    background-size: 16px 16px;
    background-repeat: no-repeat;
	}
	.decrease1{
		background-image:url("./decrease_1@2x.png");
	}
	.discount1{
		background-image:url("./discount_1@2x.png");
	}
	.guarantee1{
		background-image:url("./guarantee_1@2x.png");
	}
	.invoice1{
		background-image:url("./invoice_1@2x.png");
	}
	.special1{
		background-image:url("./special_1@2x.png");
	}
	.decrease2{
		background-image:url("./decrease_2@2x.png");
	}
	.discount2{
		background-image:url("./discount_2@2x.png");
	}
	.guarantee2{
		background-image:url("./guarantee_2@2x.png");
	}
	.invoice2{
		background-image:url("./invoice_2@2x.png");
	}
	.special2{
		background-image:url("./special_2@2x.png");
	}
	.decrease3{
		background-image:url("./decrease_3@2x.png");
	}
	.discount3{
		background-image:url("./discount_3@2x.png");
	}
	.guarantee3{
		background-image:url("./guarantee_3@2x.png");
	}
	.invoice3{
		background-image:url("./invoice_3@2x.png");
	}
	.special3{
		background-image:url("./special_3@2x.png");
	}
	.decrease4{
		background-image:url("./decrease_4@2x.png");
	}
	.discount4{
		background-image:url("./discount_4@2x.png");
	}
	.guarantee4{
		background-image:url("./guarantee_4@2x.png");
	}
	.invoice4{
		background-image:url("./invoice_4@2x.png");
	}
	.special4{
		background-image:url("./special_4@2x.png");
	}
</style>



